// Respiro API Types

export type RiskLevel = "low" | "moderate" | "high" | "severe" | "emergency"

export interface PatientState {
  patient_id: string
  session_id: string
  current_risk_level: RiskLevel
  risk_score: number
  risk_factors: string[]
  timestamp: string
}

export interface ClinicalRecommendation {
  zone: "green" | "yellow" | "red"
  zone_description: string
  actions: string[]
  medications: MedicationAction[]
  monitoring: string[]
  emergency?: boolean
  emergency_contact?: { action: string; number: string }
}

export interface MedicationAction {
  type: "controller" | "rescue"
  action: "continue" | "increase" | "immediate"
  medications?: string[]
  frequency?: string
}

export interface Recommendations {
  zone: string
  risk_score: number
  risk_level: RiskLevel
  risk_factors: string[]
  recommendations: ClinicalRecommendation
  requires_approval: boolean
  timestamp: string
}

export interface CalendarEvent {
  id: string
  summary: string
  start: { dateTime: string; timeZone: string }
  end: { dateTime: string; timeZone: string }
  location?: string
  description?: string
}

export interface RewardsStatus {
  adherence_score: number
  points: number
  rewards: Reward[]
  timestamp: string
}

export interface Reward {
  type: string
  value: string
}

export interface MemoryItem {
  text: string
  metadata: {
    type?: string
    category?: string
    [key: string]: any
  }
}

export interface ApprovalRequest {
  request_id: string
  action: string
  context: Record<string, any>
  status: "pending" | "approved" | "rejected"
  created_at: string
  timeout_at: string
}

export interface SensorData {
  air_quality: {
    aqi: number
    category: string
    pollutants: Record<string, any>
    location: { latitude: number; longitude: number }
    timestamp: string
  }
  pollen: {
    overall_risk: string
    tree_pollen: number
    grass_pollen: number
    weed_pollen: number
    location: { latitude: number; longitude: number }
    timestamp: string
  }
  biometrics: {
    heart_rate?: number
    respiratory_rate?: number
    oxygen_saturation?: number
    peak_flow?: number
    timestamp: string
  }
  timestamp: string
  data_completeness: {
    aqi: boolean
    pollen: boolean
    biometrics: boolean
  }
}

export interface NegotiatorResponse {
  response: string
  calendar_actions: CalendarAction[]
  timestamp: string
}

export interface CalendarAction {
  action: string
  event_id: string
  new_time: string
}

// Risk level utilities
export const RISK_LEVELS = {
  low: { color: "#22c55e", bg: "bg-green-500", label: "Low Risk" },
  moderate: { color: "#eab308", bg: "bg-yellow-500", label: "Moderate Risk" },
  high: { color: "#f97316", bg: "bg-orange-500", label: "High Risk" },
  severe: { color: "#ef4444", bg: "bg-red-500", label: "Severe Risk" },
  emergency: { color: "#b91c1c", bg: "bg-red-700", label: "Emergency" },
} as const

export function getRiskLevelColor(level: RiskLevel): string {
  return RISK_LEVELS[level]?.color || RISK_LEVELS.low.color
}

export function getRiskLevelBg(level: RiskLevel): string {
  return RISK_LEVELS[level]?.bg || RISK_LEVELS.low.bg
}